.root {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 10px;
}

.start_line {
  flex-grow: 1;
  height: 1px;
  background: linear-gradient(
    90deg,
    rgba(46, 43, 74, 0) 0%,
    rgba(72, 53, 234, 1) 100%
  );
}

.text {
  text-align: center;
  width: min-content;
  flex-grow: 1;
  font-size: 16px;
  line-height: 19px;
  color: #ffffff;
}

.end_line {
  height: 1px;
  flex-grow: 1;
  background: linear-gradient(
    90deg,
    rgba(72, 53, 234, 1) 0%,
    rgba(46, 43, 74, 0) 100%
  );
}
