@import "sass/index";

.error {
  border-color: #a3000099;
}

.formInput {
  width: 100%;
  margin-top: spacing(2);
}

.formInputField {
  margin-top: spacing(3);
}

.passwordFormInputField {
  & > input {
    padding-right: spacing(8.5);
  }
}

.formInputAdornment {
  position: absolute;
  right: 0;
}

.formInputAdornmentIcon {
  height: spacing(6.5);
  width: spacing(6.5);
  position: absolute;
  right: 0;
}

.formInputAdornmentIcon {
  height: spacing(6.5);
  width: spacing(6.5);
  position: absolute;
  right: 0;

  & path {
    fill: #484950;
  }
}
