@import "sass/index";

.errorWrapper {
  margin-top: spacing(1.5);
}
.linkWrapper {
  margin-top: 16px;
  height: 15px;
}
.recoverText {
  color: rgba(#ffffff, 0.3);
  font-family: Helvetica;
  font-size: 13px;
  line-height: 13px;
}
.link {
  text-decoration: underline;
}
.descriptionLink {
  color: rgba(white, 0.5);
  font-family: Helvetica;
  font-size: 13px;
  line-height: 13px;
  text-decoration: underline;
}
.reCaptchaWrapper {
  margin-top: spacing(1.5);
  width: 100%;
  height: 74px;
  position: relative;
  & > div {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    & iframe {
      position: absolute;
      width: 100%;
      height: 100%;
      border-radius: 4;
    }
  }
}
