@import "sass/index";
@import "components/Input/_input.module.scss";

.formInputLabel {
  color: rgba($m_secondary-main, 0.75);

  &:global.Mui-focused {
    color: rgba($m_secondary-main, 0.75);
  }
}

.reCaptchaWrapper {
  margin-top: spacing(1.5);
}
.errorWrapper {
  margin-top: spacing(1.5);
}
.checkboxWrapper {
  align-items: flex-start;
  margin-top: spacing(3);
  margin-left: spacing(-0.5);
  align-self: flex-start;
}
.checkbox {
  width: 24px;
  height: 24px;
  background-image: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0.06) 0%,
    rgba(255, 255, 255, 0.08) 100%
  ) !important;
  border-color: #4b4857 !important;
  border-width: 1px;
  border-radius: 4px;
  &Checked {
    background-color: transparent !important;
  }
  &Icon {
    transform: scale(1);
    & > path {
      fill: #4b4857;
    }
  }
}
.checkboxLabelWrapper {
  font-size: 13px;
  font-family: Helvetica;
  color: rgba(white, 0.3);
  padding-left: 8px;
}
.link {
  color: rgba(white, 0.3);
}
.descriptionLink {
  color: rgba(white, 0.5);
  font-family: Helvetica;
  font-size: 13px;
  line-height: 13px;
  text-decoration: underline;
}

.stepContainer {
  width: 100%;
  position: relative;
  @media (max-width: 600px) {
    height: 100vh;
  }
}
.arrow {
  width: 26px;
  height: 26px;
  position: absolute;
  color: rgba(white, 0.5);
  top: 20px;
  left: 20px;
  z-index: 1;
}
