.root {
  position: absolute;
  z-index: 1;
  top: 0px;
  left: 0px;
  height: 100%;
  width: 100%;
  display: flex;

  align-items: center;
  background: rgba(0, 0, 0, 0.4);
}

.header_container {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header {
  color: #ffffff;
  font-size: 22px;
  line-height: 30px;
}

.container {
  min-height: 150px;
  width: 100%;
  margin: 0px 20px;
  padding: 20px 30px;
  background: linear-gradient(
      135deg,
      #3b1871 0%,
      rgba(11, 4, 21, 0.181709) 48.48%,
      rgba(3, 1, 4, 0.0001) 80.79%,
      #000000 100%
    ),
    #0a0d18;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
}

.checkboxWrapper {
  align-items: flex-start;
  margin-top: 15px;
  align-self: flex-start;
}

.checkbox {
  width: 24px;
  height: 24px;
  background-image: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0.06) 0%,
    rgba(255, 255, 255, 0.08) 100%
  );
  border-color: rgba(255, 255, 255, 0.1);
  border-width: 1px;
  border-radius: 4px;
  &Checked {
    background-color: transparent;
  }
  &Icon {
    transform: scale(1);
    & > path {
      fill: rgba(255, 255, 255, 0.1);
    }
  }
}

.checkboxLabelWrapper {
  font-size: 13px;
  font-family: Helvetica;
  color: rgba(white, 0.3);
  padding-left: 8px;
}
.link {
  color: rgba(white, 0.3);
}
