.root {
  width: 44px;
  height: 44px;
  border-radius: 100%;
  border: 1px solid rgba(255, 255, 255, 0.1);
  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0.0620629) 0%,
    rgba(255, 255, 255, 0.0755026) 100%
  );
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  &:hover {
    opacity: 0.8;
  }
}

.disabled {
  cursor: default;
  opacity: 0.6;
  &:hover {
    opacity: 0.6;
  }
}

.img {
  width: 26px;
  height: 26px;
}
